import React from 'react';
import Image from 'next/image';

const AboutSection = () => {
  return (
    <section className="bg-white py-16 px-4 md:px-8 lg:px-20">
      <div className="max-w-7xl mx-auto">
        {/* Existing content (you can customize this) */}
        <div className="mb-16">
          <h2 className="text-3xl font-bold text-blue-800 mb-6">About the NTC</h2>
          <p className="text-gray-700 leading-relaxed">
            The Nautical Training Corps is a youth organisation dedicated to providing unique opportunities in a structured and engaging environment.
          </p>
          {/* Add your original content here if needed */}
        </div>

        {/* ðŸ”¥ New added block: Our History */}
        <div className="grid md:grid-cols-2 gap-12 items-center mt-12">
          <div>
            <h3 className="text-2xl font-semibold text-blue-700 mb-4">Our History</h3>
            <p className="text-gray-700 leading-relaxed">
              Founded in 1944 by Major Lionel E. Willis, the Nautical Training Corps has been educating young people in leadership, discipline, and maritime skills for over 80 years.
              Through our local units across the UK, we continue to pass on strong values through a wide variety of guided activities.
            </p>
            <p className="text-gray-700 mt-4 leading-relaxed">
              Our programs are designed to foster personal growth, teamwork, and responsibility in youth from all backgrounds.
            </p>
          </div>

          <div className="relative w-full h-120 md:h-96">
            <Image
              src="/images/ntc-history.jpg"
              alt="Historical NTC photo"
              layout="fill"
              objectFit="cover"
              className="rounded-lg shadow-lg"
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default AboutSection;
