import React from 'react';
import Link from 'next/link';
import { Flag } from 'lucide-react';

const JoinNowSection = () => {
  return (
    <section className="bg-blue-800 text-white py-20 px-4 md:px-8 text-center">
      <div className="max-w-3xl mx-auto">
        <div className="flex justify-center mb-6">
          <Flag className="w-10 h-10 text-white" />
        </div>

        <h2 className="text-3xl md:text-4xl font-bold mb-4">Ready to Join the Corps?</h2>
        <p className="text-lg mb-8">
          Become part of a proud tradition of leadership, adventure, and maritime excellence. Your journey starts today.
        </p>

        <Link
          href="/join"
          className="inline-block bg-white text-blue-800 font-semibold px-8 py-3 rounded-lg shadow hover:bg-gray-100 transition"
        >
          Join Now
        </Link>
      </div>
    </section>
  );
};

export default JoinNowSection;
